import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import { withRouter } from 'react-router-dom';

import "./index.scss";
import Anime from "../../components/Anime";
import Loading from "../../components/Loading";
import Posts from "../Posts";

@inject("store", "categoryStore")
@withRouter
@observer
export default class index extends Component {
	constructor(props) {
		super(props);
		this.state = {
			category: []
		};
	}

	componentWillMount() {
		this.props.store.loadPosts("Ova,Movie");
		this.props.categoryStore.pullCategory().then(() => {
			let tmp = this.props.categoryStore.category.filter((label) => {
				label = label.toLowerCase();
				return label !== "Ova" || label !== "Movie";
			});
			this.setState({category: tmp});
		});
	}

	render() {
		const {category} = this.state;
		const {isLoading} = this.props.categoryStore;
		const {isLoading: loading} = this.props.store;
		return isLoading && loading ? <Loading /> : (
			<div className="anime-list">
				<div className="row">
					<div className="col-xs-12">
						<h3 className="section-title">Аниме цуврал</h3>
					</div>
					{category.map((label, index) => (
						<div key={`category-${index}`} className="col-xs-12 col-sm-4 col-md-2">
							<Anime label={label} />
						</div>
					))}
				</div>
				<div className="row">
					<Posts />
				</div>
			</div>
		);
	}
}



